@import "./includes/_imports.css";

/* Landing Page */
.landing-page {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.landing-page .bg-img-1 {
  position: absolute;
  top: -100px;
  left: 0px;
  width: 50%;
}

.landing-page .bg-img-2 {
  position: absolute;
  top: 20%;
  right: 0px;
  width: 15%;
}

.landing-page .bg-img-3 {
  position: absolute;
  top: 30%;
  left: 0px;
  width: 50%;
}

.landing-page .bg-img-4 {
  position: absolute;
  top: 50%;
  right: 0px;
  width: 40%;
}

.landing-page nav {
  position: relative;
  z-index: 2;
  margin-top: 100px;
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
}

.landing-page nav .container {
  height: 70px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.landing-page nav .container .nav-left {
  width: 500px;
  padding: 0px 50px;
  display: flex;
  align-items: center;
}

.landing-page nav .container .nav-left ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.landing-page nav .container .nav-left ul li {
  position: relative;
  margin-right: 30px;
}

.landing-page nav .container .nav-left ul li:last-child {
  margin-right: 0px;
}

.landing-page nav .container .nav-left ul li::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  height: 3px;
  background: linear-gradient(
    50deg,
    var(--secondary-color),
    var(--primary-color)
  );
  border-radius: 100px;
  opacity: 0;
  transition: all 0.1s ease;
}

.landing-page nav .container .nav-left ul li.active::after {
  opacity: 1;
}

.landing-page nav .container .nav-left ul li a {
  font-size: 16px;
  font-weight: 500;
  color: #9a99ad;
  cursor: pointer;
  transition: all 0.3s ease;
}

.landing-page nav .container .nav-left ul li a:hover {
  color: var(--primary-text-color);
}

.landing-page nav .container .nav-left ul li.active a {
  color: var(--primary-text-color);
}

.landing-page nav .container .nav-center {
  width: 150px;
  position: relative;
}

.landing-page nav .container .nav-center img {
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  width: 100%;
}

.landing-page nav .container .nav-right {
  width: 500px;
  padding: 0px 0px 0px 50px;
  display: flex;
  align-items: center;
}

.landing-page nav .container .nav-right ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.landing-page nav .container .nav-right ul li {
  margin-right: 30px;
}

.landing-page nav .container .nav-right ul li:last-child {
  margin-right: 0px;
}

.landing-page nav .container .nav-right ul li.active {
  position: relative;
}

.landing-page nav .container .nav-right ul li.active::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  height: 3px;
  background: linear-gradient(
    50deg,
    var(--secondary-color),
    var(--primary-color)
  );
  border-radius: 100px;
}

.landing-page nav .container .nav-right ul li a {
  font-size: 16px;
  font-weight: 500;
  color: #9a99ad;
  cursor: pointer;
  transition: all 0.3s ease;
}

.landing-page nav .container .nav-right ul li a:hover {
  color: var(--primary-text-color);
}

.landing-page nav .container .nav-right ul li.active a {
  color: var(--primary-text-color);
}

.landing-page nav .container .nav-right ul li button {
  width: 200px;
  height: 70px;
  background: linear-gradient(
    50deg,
    var(--secondary-color),
    var(--primary-color)
  );
  border-radius: 100px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.landing-page nav .container .nav-right ul li button:hover {
  box-shadow: 0px 0px 50px rgba(132, 84, 235, 0.5);
}

.landing-page .hero {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page .hero .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing-page .hero .container .mint-box {
  width: 100%;
  padding: 100px 50px 50px 50px;
  background: rgba(12, 52, 120, 0.05);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}

.landing-page .hero .container .mint-box img {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  opacity: 0.1;
}

.landing-page .hero .container .mint-box h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  margin-bottom: 40px;
}

.landing-page .hero .container .mint-box .mint-box-top-text {
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  color: rgba(255, 255, 255, 0.4);
  margin-bottom: 50px;
}

.landing-page .hero .container .mint-box .quantity-box {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
  margin-bottom: 10px;
}

.landing-page .hero .container .mint-box .quantity-box .quantity-box-left {
  width: 100%;
  display: flex;
}

.landing-page
  .hero
  .container
  .mint-box
  .quantity-box
  .quantity-box-left
  button {
  min-width: 50px;
  max-width: 50px;
  height: 50px;
  border: 1px solid var(--primary-color);
  background: transparent;
  border-radius: 5px;
  font-size: 20px;
  color: var(--primary-color);
  transition: all 0.3s ease;
}

.landing-page
  .hero
  .container
  .mint-box
  .quantity-box
  .quantity-box-left
  button:hover {
  opacity: 0.6;
}

.landing-page .hero .container .mint-box .quantity-box .quantity-box-left span {
  flex: 1;
  margin: 0px 10px;
  width: 100%;
  height: 50px;
  background: #343444;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
}

.landing-page .hero .container .mint-box .mint-btn {
  width: 100%;
  height: 50px;
  border: 1px solid var(--primary-color);
  background: var(--primary-color);
  border-radius: 5px;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease;
  margin-bottom: 20px;
  opacity: 0.5;
  pointer-events: none;
}

.landing-page .hero .container .mint-box .mint-btn.active {
  opacity: 1;
  pointer-events: all;
}

.landing-page .hero .container .mint-box .mint-btn:hover {
  background: transparent;
  color: var(--primary-color);
}

.landing-page .hero .container .mint-box .mint-box-bottom-text {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  color: var(--secondary-text-color);
}

.landing-page .hero .container .mint-box .mint-box-bottom-text span {
  font-weight: 500;
  color: var(--primary-color);
}

.landing-page .about {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page .about .container {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 100px;
}

.landing-page .about .container .about-left {
  width: 100%;
}

.landing-page .about .container .about-left .about-img-1 {
  width: 100%;
}

.landing-page .about .container .about-right {
  width: 100%;
}

.landing-page .about .container .about-right .title {
  width: 80%;
  margin-bottom: 40px;
}

.landing-page .about .container .about-right p {
  font-size: 24px;
  font-weight: 300;
  color: var(--secondary-text-color);
}

.landing-page .roadmap {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page .roadmap .container .roadmap-img-1 {
  width: 100%;
}

.landing-page .team {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page .team .bg-img-6 {
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-20%);
  width: 100%;
}

.landing-page .team .container .team-img-1 {
  width: 100%;
  position: relative;
  z-index: 2;
}

.landing-page .faq {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page .faq .container .faq-img-1 {
  width: 100%;
}

.landing-page .faq .container .faq-img-1.mobile-v {
  display: none;
}

.landing-page footer {
  position: relative;
  z-index: 2;
  margin-top: 150px;
  margin-bottom: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.landing-page footer .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing-page footer .container .logo {
  display: flex;
  width: 100px;
  margin-bottom: 30px;
}

.landing-page footer .container .logo img {
  width: 100%;
}

.landing-page footer .container .social-links {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.landing-page footer .container .social-links a {
  transition: all 0.3s ease;
  margin-right: 10px;
}

.landing-page footer .container .social-links a:hover {
  transform: translateY(-5px);
}

.landing-page footer .container .social-links a svg path {
  transition: all 0.3s ease;
}

.landing-page footer .container .social-links a:hover svg path {
  fill: var(--primary-color);
}

.landing-page footer .container .social-links a:last-child {
  margin-right: 0px;
}

.landing-page footer .container p {
  font-size: 18px;
  font-weight: 300;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}

/* Responsive */
@media only screen and (max-width: 1220px) {
  .landing-page nav {
    margin-top: 50px;
  }

  .landing-page nav .container {
    height: 50px;
  }

  .landing-page nav .container .nav-left {
    display: none;
  }

  .landing-page nav .container .nav-center {
    width: 100px;
  }

  .landing-page nav .container .nav-right {
    display: none;
  }

  .landing-page .hero {
    margin-top: 100px;
  }

  .landing-page .about {
    margin-top: 100px;
  }

  .landing-page .roadmap {
    margin-top: 100px;
  }

  .landing-page .team {
    margin-top: 100px;
  }

  .landing-page .faq {
    margin-top: 100px;
  }

  .landing-page footer {
    margin-top: 100px;
  }
}

@media only screen and (max-width: 1024px) {
  .landing-page .about .container {
    grid-template-columns: 1fr;
    grid-gap: 50px;
  }

  .landing-page .about .container .about-right {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .landing-page .about .container .about-right p {
    text-align: center;
  }
}

@media only screen and (max-width: 850px) {
  .landing-page .hero .container .title.desktop-v {
    display: none;
  }

  .landing-page .hero .container .title.mobile-v {
    display: block;
  }

  .landing-page .hero .container button {
    margin-bottom: 50px;
  }

  .landing-page .hero .container .hero-img-1.desktop-v {
    display: none;
  }

  .landing-page .hero .container .hero-img-1.mobile-v {
    display: block;
  }

  .landing-page .about .container .about-right .title {
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
  .landing-page .hero .container .timer p {
    font-size: 26px;
  }

  .landing-page .about .container .about-right p {
    font-size: 20px;
  }

  .landing-page .faq .container .faq-img-1.desktop-v {
    display: none;
  }

  .landing-page .faq .container .faq-img-1.mobile-v {
    display: block;
  }
}
