* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #04031a;
  font-family: var(--primary-font-family);
  color: var(--primary-text-color);
}

::selection {
  background: var(--primary-color);
  color: var(--primary-text-color);
}

a {
  text-decoration: none;
  color: var(--primary-text-color);
}

li {
  list-style: none;
}

button,
input {
  background: none;
  border: none;
  outline: none;
  font-family: var(--primary-font-family);
  color: var(--primary-text-color);
}

button {
  cursor: pointer;
}

img {
  user-select: none;
}
